
body {
  background-image: url('bg.png');
  background-color: rgb(15, 33, 59);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.main-container {
  background-image: linear-gradient(to right, rgb(28, 43, 70) , rgb(76, 84, 123));
  width: 720px;
  height: 480px;
  display: grid;
  justify-items: center;
}

.grid-item {
  background-color: rgb(13, 26, 47);
  width: 40px;
  height: 40px;
  margin: 2px;
  text-align: center;
  font-size: 11px;
  border-radius: 5px;
  position: relative;
  color: white;
}

.grid-item-combine {
  background-image: url('combine-slot-bg.png');
  background-size: 40px 40px;
  width: 40px;
  height: 40px;
  margin: 2px;
  text-align: center;
  border-radius: 5px;
}

.item-combine-preview {
  background-image: url('unknown-bg.png');
  background-size: 40px 40px;
}

.grid-container { 
  display: inline-grid;
  grid-template-columns: auto auto auto auto;
  padding: 10px;
}

.result-grid-container { 
  display: inline-grid;
  justify-items: end;
  width: 720px;
  background-color: rgba(0, 0, 0, 0.485);
}

.result-grid-container-1 { 
  grid-template-columns: auto;
  justify-content: center;
}

.result-grid-container-2 { 
  grid-template-columns: auto auto;
  justify-content: center;
}

.result-grid-container-3 { 
  grid-template-columns: auto auto auto;
  justify-content: center;
}

.result-grid-container-4 { 
  grid-template-columns: auto auto auto auto;
  justify-content: center;
}

.result-grid-container-5 { 
  grid-template-columns: auto auto auto auto auto;
  justify-content: center;
}

.color-red {
  color: red;
}

.result-container {
  width: 720px;
  background-image: url('result-bg.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 300%;
  display: grid;
  grid-template-rows: auto;
  justify-items: center;
}

.result-grid-item {
  margin: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.incense-burner {
  width: 50px;
  font-size: 11px;
  position: relative;
}

.burner-text {
  margin-left: 10px;
}

.incense-burners {
  display: inline-grid;
  grid-template-columns: auto auto;
  width: 120px;
  font-size: 11px;
}

.bottom-right-text {
  position: absolute;
  bottom: 0px;
  right: 0px;
}

.glow-box {
  animation: neonBox 1.5s ease-in-out infinite alternate;
}

.glow-box-2 {
  animation: neonBox2 1.5s ease-in-out infinite alternate;
}

.result-preview {
  height: 40px; 
  width: 40px;
}

.glow {
  animation: neon 1.5s ease-in-out infinite alternate;
}

.slot-container {
  height: 400px;
  overflow-y: scroll;
}

.combine-section {
  width: 290px;
}

.combine-actions {
  display: inline-grid;
  grid-template-columns: auto auto auto;
}

.combine-container {
  height: 290px;
  overflow-y: scroll;
}

.note {
  font-size: 10px;
}

.button {
  height: 40px;
  width: 150px;
  border-color: #ffdd1b;
  background-color: rgb(49, 37, 7);
  color: #fff;
  font-weight: bold;
  border-radius: 100px;
  animation: neonBox 1.5s ease-in-out infinite alternate;
}

.button-sm {
  height: 40px;
  width: 90px;
  border-color: #ffdd1b;
  background-color: rgb(12, 39, 58);
  color: #fff;
  font-weight: bold;
  font-size: 11px;
  border-radius: 100px;
  margin: 3px;
}

.combination-result-text {
  border-top:  1px solid #ffdd1b;
  border-bottom:  1px solid #ffdd1b;
  padding: 10px;
  width: 400px;
  background-color: #846012af;
  font-weight: 400;
  height: 35px;
  text-align: center;
}

@keyframes neonBox {
  from {
    box-shadow: 0 0 3px #fff, 0 0 5px #fff, 0 0 7px #fff, 0 0 10px #ffdd1b,
      0 0 12px #ffdd1b, 0 0 15px #ffdd1b, 0 0 18px #ffdd1b, 0 0 20px #ffdd1b;
  }
  to {
    box-shadow: 0 0 5px #fff, 0 0 3px #fff, 0 0 5px #fff, 0 0 7px #ffdd1b,
      0 0 9px #ffdd1b, 0 0 12px #ffdd1b, 0 0 15px #ffdd1b, 0 0 18px #ffdd1b;
  }
}

@keyframes neonBox2 {
  from {
    box-shadow: 0 0 1px #fff, 0 0 1.5px #fff, 0 0 1.8px #fff, 0 0 2px #ffdd1b,
      0 0 2.4px #ffdd1b, 0 0 2.7px #ffdd1b, 0 0 2.8px #ffdd1b, 0 0 3px #ffdd1b;
  }
  to {
    box-shadow: 0 0 2px #fff, 0 0 2.1px #fff, 0 0 2.3px #fff, 0 0 2.6px #ffdd1b,
      0 0 2.8px #ffdd1b, 0 0 2.9px #ffdd1b, 0 0 3.0px #ffdd1b, 0 0 3.2px #ffdd1b;
  }
}

@keyframes neon {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #ffdd1b,
      0 0 70px #ffdd1b, 0 0 80px #ffdd1b, 0 0 100px #ffdd1b, 0 0 150px #ffdd1b;
  }
  to {
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #ffdd1b,
      0 0 35px #ffdd1b, 0 0 40px #ffdd1b, 0 0 50px #ffdd1b, 0 0 75px #ffdd1b;
  }
}